.banner-se {
  height: 24rem;
  position: relative;
}

.icon-f {
  width: 2rem;
  height: 2rem;
  float: right;
}

.form-bn {
  width: 42rem;
  background-color: white;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.input-group {
  border-bottom: 3px solid blue;
  overflow: auto;
  padding: 1.3rem 1.3rem 1.3rem 2rem;
}

.input-group input {
  float: left;
  font-size: 1.5rem;
  width: 35rem;
  border-style: none;
}

.input-group .icon-f {
  float: right;
}

.button-group {
  padding: 1.8rem;
  overflow: auto;
}

.button-group button {
  float: right;
  padding: 1rem;
  margin: 0 1rem;
  border: none;
  font-size: 1.2rem;
}

.button-group button:active {
  box-shadow: 0.2rem 0.2rem 0.3rem 0.1rem #333 inset;
}

.search {
  background-color: #00b8e8;
  color: white;
}

.reset {
  background-color: white;
}
