.navbar {
  width: 70rem;
  background: rgba(0, 0, 0, 0.05);
  overflow: auto;
  padding: 0.5rem 1rem;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}

.navbar-tt {
  width: 70rem;
  background: rgba(0, 0, 0, 0.8);
  overflow: auto;
  padding: 0.5rem 1rem;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}

.icon {
  width: 2rem;
  height: 2rem;
  float: right;
}

.a {
  text-decoration: none;
  float: left;
  color: red;
  font-weight: bold;
  font-size: 1.5rem;
}
