@media (max-width: 1024px) {
  .App {
    width: 100%;
    margin: 0 auto;
  }
  .layout {
    width: 100%;
  }
  .navbar {
    width: 100vw;
    background: rgba(0, 0, 0, 0.05);
    overflow: auto;
    padding: 0.5rem 1rem;
    position: fixed;
    top: 0;
    margin: 0 auto;
    z-index: 10;
  }
  .app {
    width: 100%;
    margin: 0 auto;
  }
  #root {
    width: 100%;
    margin: 0 auto;
  }
  body {
    width: 100%;
    background-color: #222;
    box-sizing: border-box;
    margin: 0 auto;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  html {
    font-size: 2vw;
    display: block;
    width: 99vw;
    margin: 0 auto;
  }
  .movie-detail {
    display: flex;
    width: 100%;
    margin-top: 15px;
    aspect-ratio: 3/1;
    color: white;
    justify-content: space-between;
  }
  .detail-text {
    width: 48%;
    height: 24rem;
    padding: 10px;
  }
}
