.movie-detail {
  display: flex;
  width: 100%;
  margin-top: 15px;
  padding: 1rem 0;
  aspect-ratio: 3/1;
  color: white;
  justify-content: space-between;
}

.detail-text {
  width: 48%;
  padding: 10px;
}

.video-frame {
  width: 48%;
  aspect-ratio: 4/3;
  padding: 1rem;
}

#videoId {
  width: 100%;
  height: 100%;
}

.video {
  width: 100%;
  height: 100%;
  background-color: aqua;
}

.detail-text h2 {
  border-bottom: 0.2rem solid white;
  padding-bottom: 1.2rem;
}

.detail-text h4 {
  margin: 0;
}

.detail-text p {
  font-size: 1.3rem;
  height: 100%;
}
