.banner {
  /* height: 26rem; */
  aspect-ratio: 4/1;
  padding: 1.8rem;
  position: relative;
}

.text {
  color: white;
  width: 28rem;
  position: absolute;
  bottom: 1rem;
}

.p p {
  text-overflow: clip;
}

.p {
  height: 8rem;
  overflow: hidden;
}

.text button {
  color: white;
  margin: 1rem;
  padding: 0.6rem 0.8rem;
  background: rgba(0, 0, 0, 0.3);
}
