.danh-muc {
  width: 100%;
  height: 10rem;
  padding: 0;
  overflow-x: auto;
}

.danh-muc::-webkit-scrollbar {
  height: 0.8rem;
  background-color: #000;
}

.danh-muc::-webkit-scrollbar-thumb {
  background-color: #333;
}

.content {
  display: flex;
  gap: 1rem;
  height: 9rem;
}

.content img {
  height: 100%;
  aspect-ratio: 4/3;
}

.content img:hover {
  transform: scale(1.1, 1.1);
}

.title {
  margin-top: 1.5rem;
  color: white;
}

.danh-muc-ori {
  width: 100%;
  height: 18rem;
  padding: 0;
  margin-top: 2.5rem;
  overflow-x: auto;
}

.danh-muc-ori::-webkit-scrollbar {
  height: 0.8rem;
  background-color: #000;
}

.danh-muc-ori::-webkit-scrollbar-thumb {
  background-color: #333;
}

.content-ori {
  display: flex;
  gap: 1rem;
  height: 17rem;
}

.content-ori img {
  height: 100%;
  aspect-ratio: 3/5;
}

.content-ori img:hover {
  transform: scale(1.2, 1.2);
}
